import React, { useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import modelItem from '../images/three/scene.gltf';

import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';
import styled from 'styled-components';

const Home = () => {
  useEffect(() => {
    //  .
    let scene = new THREE.Scene();

    //     .
    // 3d     WebGL     .
    let renderer = new THREE.WebGLRenderer({
      canvas: document.querySelector('#canvas'),
      antialias: true,
    });
    renderer.outputEncoding = THREE.sRGBEncoding;

    //  
    // PerspectiveCamera ( O)
    // OrthographicCamera ( )
    let camera = new THREE.PerspectiveCamera(25, 1);
    camera.position.set(0, 0, 8);

    //  & 
    scene.background = new THREE.Color('white');
    let light = new THREE.DirectionalLight(0x446ab3, 0.8);
    let light2 = new THREE.AmbientLight(0xffffff, 0.6);
    scene.add(light);
    scene.add(light2);

    let loader = new GLTFLoader();
    loader.load(modelItem, function (gltf) {
      scene.add(gltf.scene);

      function animate() {
        requestAnimationFrame(animate);
        gltf.scene.rotation.y += 0.015;
        renderer.render(scene, camera);
      }
      animate();
    });
  }, []);
  return (
    <HomeWrap>
      <Inner>
        <p>
           
          <br />
               .
          <br />
          <span>  React</span> ,
          <br />
          <span>Javascript  CSS Keyframe animation, Three.js, nivo </span> 
          .
        </p>
        <div>
          <canvas id="canvas" width="400" height="400"></canvas>
        </div>
      </Inner>
      {/* <Skeleton /> */}
    </HomeWrap>
  );
};

export default Home;

const HomeWrap = styled.div`
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: space-between;

  canvas {
    display: block;
    margin: auto;
  }

  @media ${({ theme }) => theme.device.mobile} {
    canvas {
      width: 100%;
    }
  }
`;

const Inner = styled.div`
  width: 60rem;
  margin: auto;

  p {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.5;
    color: #3a3a3a;

    span {
      color: #446ab3;
    }
  }

  @media ${({ theme }) => theme.device.mobile} {
    p {
      font-size: 2.6rem;
    }
  }
`;
